import { Axios } from 'axios'
import {
  VaultType,
  VaultTypeData,
  VaultTypeRoutes,
  VaultTypesClientModel,
  VaultTypesSearchQuery,
} from '../vault-type.model'
import { ApiEntity } from '../../core'

export class VaultTypesClient
  extends ApiEntity
  implements VaultTypesClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Create a vault type
   * @param body - the type data
   * @returns the created type
   */
  public create(body: VaultTypeData) {
    return this._create<VaultType>(VaultTypeRoutes.v1Create, body)
  }

  /**
   * Search types by params
   * @param searchParams - the filters to apply
   * @returns a types page
   */
  public search(searchParams?: VaultTypesSearchQuery) {
    return this._search<VaultType>(VaultTypeRoutes.v1Search, searchParams)
  }

  /**
   * List types by params
   * @param searchParams - the filters to apply
   * @returns the types list
   */
  public list(searchParams?: VaultTypesSearchQuery) {
    return this._list<VaultType>(VaultTypeRoutes.v1Search, searchParams)
  }

  /**
   * Find a type by params
   * @param searchParams - the filters to apply
   * @returns an optional type
   */
  public findOne(searchParams?: VaultTypesSearchQuery) {
    return this._findOne<VaultType>(VaultTypeRoutes.v1Search, searchParams)
  }
}
